"use client"

import { useState } from 'react'

interface Patient {
  id: string
  name: string
  mobile: string
  nextAppointment: string
  department: string
  doctor: string
}

export default function ReminderSystem() {
  const [selectedPatients, setSelectedPatients] = useState<string[]>([])
  const [reminderType, setReminderType] = useState('appointment')
  const [customMessage, setCustomMessage] = useState('')
  
  // Sample patient data
  const patients: Patient[] = [
    {
      id: '1',
      name: 'Mr. Yogesh Varma',
      mobile: '9769648154',
      nextAppointment: '2024-01-15',
      department: 'Day Care',
      doctor: 'Dr. Chetan Khandala'
    },
    {
      id: '2',
      name: 'Mrs. Priya Sharma',
      mobile: '9876543210',
      nextAppointment: '2024-01-16',
      department: 'Yoga',
      doctor: 'Dr. Chetan Khandala'
    },
    {
      id: '3',
      name: 'Mr. Amit Patel',
      mobile: '9123456789',
      nextAppointment: '2024-01-17',
      department: 'Day Care',
      doctor: 'Dr. Chetan Khandala'
    }
  ]

  const reminderTemplates = {
    appointment: `ðŸ¥ *Vaidya Manager - Appointment Reminder*

Dear {name},

This is a friendly reminder about your upcoming appointment:

ðŸ“… *Date:* {date}
ðŸ¥ *Department:* {department}
ðŸ‘¨â€âš•ï¸ *Doctor:* {doctor}

Please arrive 15 minutes early for registration.

For any changes, please contact us.

Thank you!
*Vaidya Manager Team*`,

    followup: `ðŸ”” *Vaidya Manager - Follow-up Reminder*

Dear {name},

Hope you are feeling better! 

This is a gentle reminder for your follow-up appointment:

ðŸ“… *Date:* {date}
ðŸ¥ *Department:* {department}
ðŸ‘¨â€âš•ï¸ *Doctor:* {doctor}

Please bring your previous reports and medications.

*Vaidya Manager Team*`,

    medication: `ðŸ’Š *Vaidya Manager - Medication Reminder*

Dear {name},

This is a reminder to take your prescribed medications on time.

Please follow the dosage instructions given by {doctor}.

For any queries, feel free to contact us.

*Vaidya Manager Team*`,

    custom: customMessage
  }

  const handlePatientSelect = (patientId: string) => {
    setSelectedPatients(prev => 
      prev.includes(patientId) 
        ? prev.filter(id => id !== patientId)
        : [...prev, patientId]
    )
  }

  const selectAllPatients = () => {
    setSelectedPatients(patients.map(p => p.id))
  }

  const clearSelection = () => {
    setSelectedPatients([])
  }

  const generateWhatsAppMessage = (patient: Patient) => {
    let template = reminderTemplates[reminderType as keyof typeof reminderTemplates]
    
    return template
      .replace(/{name}/g, patient.name)
      .replace(/{date}/g, patient.nextAppointment)
      .replace(/{department}/g, patient.department)
      .replace(/{doctor}/g, patient.doctor)
  }

  const sendWhatsAppReminder = (patient: Patient) => {
    const message = generateWhatsAppMessage(patient)
    const encodedMessage = encodeURIComponent(message)
    const whatsappUrl = `https://wa.me/91${patient.mobile}?text=${encodedMessage}`
    window.open(whatsappUrl, '_blank')
  }

  const sendBulkReminders = () => {
    const selectedPatientData = patients.filter(p => selectedPatients.includes(p.id))
    
    selectedPatientData.forEach((patient, index) => {
      setTimeout(() => {
        sendWhatsAppReminder(patient)
      }, index * 1000) // 1 second delay between each message
    })
  }

  const previewMessage = () => {
    if (selectedPatients.length > 0) {
      const firstPatient = patients.find(p => p.id === selectedPatients[0])
      if (firstPatient) {
        return generateWhatsAppMessage(firstPatient)
      }
    }
    return ''
  }

  return (
    <div className="space-y-6">
      {/* Dashboard Widgets */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <div className="bg-purple-600 rounded-lg p-6 shadow-lg">
          <h3 className="text-white text-lg font-semibold mb-2">Follow up</h3>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-white opacity-80 rounded"></div>
            <span className="text-white text-2xl font-bold">3</span>
          </div>
        </div>
        
        <div className="bg-red-600 rounded-lg p-6 shadow-lg">
          <h3 className="text-white text-lg font-semibold mb-2">Appointment</h3>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 border-2 border-white rounded"></div>
            <span className="text-white text-2xl font-bold">0</span>
          </div>
        </div>
        
        <div className="bg-blue-600 rounded-lg p-6 shadow-lg">
          <h3 className="text-white text-lg font-semibold mb-2">Waiting list</h3>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-white opacity-80 rounded"></div>
            <span className="text-white text-2xl font-bold">0</span>
          </div>
        </div>
        
        <div className="bg-green-600 rounded-lg p-6 shadow-lg">
          <h3 className="text-white text-lg font-semibold mb-2">Medicine</h3>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-white opacity-80 rounded"></div>
            <span className="text-white text-2xl font-bold">3</span>
          </div>
        </div>
        
        <div className="bg-orange-600 rounded-lg p-6 shadow-lg">
          <h3 className="text-white text-lg font-semibold mb-2">Recent Patient</h3>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 bg-white opacity-80 rounded"></div>
            <span className="text-white text-2xl font-bold">0</span>
          </div>
        </div>
      </div>

      {/* Reminder System */}
      <div className="bg-white rounded-lg shadow p-6">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-gray-900">WhatsApp Reminder System</h2>
          <div className="flex items-center space-x-2 text-green-600">
            <div className="w-6 h-6 bg-green-500 rounded-full flex items-center justify-center">
              <span className="text-white text-xs">ðŸ“±</span>
            </div>
            <span className="font-medium">WhatsApp Integration</span>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Patient Selection */}
          <div className="lg:col-span-2">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-800">Select Patients</h3>
              <div className="space-x-2">
                <button
                  onClick={selectAllPatients}
                  className="px-3 py-1 text-sm bg-blue-100 text-blue-800 rounded-md hover:bg-blue-200"
                >
                  Select All
                </button>
                <button
                  onClick={clearSelection}
                  className="px-3 py-1 text-sm bg-gray-100 text-gray-600 rounded-md hover:bg-gray-200"
                >
                  Clear
                </button>
              </div>
            </div>

            <div className="border border-gray-200 rounded-lg overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-4 py-3 text-left">
                      <input
                        type="checkbox"
                        checked={selectedPatients.length === patients.length}
                        onChange={() => selectedPatients.length === patients.length ? clearSelection() : selectAllPatients()}
                        className="rounded border-gray-300"
                      />
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Patient Name</th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Mobile</th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Next Appointment</th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Action</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {patients.map((patient) => (
                    <tr key={patient.id} className={selectedPatients.includes(patient.id) ? 'bg-blue-50' : ''}>
                      <td className="px-4 py-3">
                        <input
                          type="checkbox"
                          checked={selectedPatients.includes(patient.id)}
                          onChange={() => handlePatientSelect(patient.id)}
                          className="rounded border-gray-300"
                        />
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-900">{patient.name}</td>
                      <td className="px-4 py-3 text-sm text-gray-600">+91 {patient.mobile}</td>
                      <td className="px-4 py-3 text-sm text-gray-600">{patient.nextAppointment}</td>
                      <td className="px-4 py-3">
                        <button
                          onClick={() => sendWhatsAppReminder(patient)}
                          className="px-3 py-1 text-xs bg-green-100 text-green-800 rounded-md hover:bg-green-200"
                        >
                          Send WhatsApp
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* Message Configuration */}
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Reminder Type
              </label>
              <select
                value={reminderType}
                onChange={(e) => setReminderType(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="appointment">Appointment Reminder</option>
                <option value="followup">Follow-up Reminder</option>
                <option value="medication">Medication Reminder</option>
                <option value="custom">Custom Message</option>
              </select>
            </div>

            {reminderType === 'custom' && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Custom Message
                </label>
                <textarea
                  value={customMessage}
                  onChange={(e) => setCustomMessage(e.target.value)}
                  placeholder="Enter your custom message..."
                  rows={4}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
            )}

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Message Preview
              </label>
              <div className="bg-gray-50 border border-gray-200 rounded-md p-3 text-sm text-gray-700 whitespace-pre-wrap max-h-40 overflow-y-auto">
                {previewMessage() || 'Select patients to preview message'}
              </div>
            </div>

            <div className="space-y-2">
              <button
                onClick={sendBulkReminders}
                disabled={selectedPatients.length === 0}
                className={`w-full px-4 py-2 rounded-md font-medium ${
                  selectedPatients.length > 0
                    ? 'bg-green-600 text-white hover:bg-green-700'
                    : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                }`}
              >
                Send to Selected ({selectedPatients.length})
              </button>
              
              <p className="text-xs text-gray-500 text-center">
                Messages will open in WhatsApp Web with 1 second intervals
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Instructions */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <h3 className="text-lg font-semibold text-blue-800 mb-2">How it works:</h3>
        <ul className="text-sm text-blue-700 space-y-1">
          <li>â€¢ Select patients from the list above</li>
          <li>â€¢ Choose a reminder type or create a custom message</li>
          <li>â€¢ Click "Send to Selected" to open WhatsApp Web for each patient</li>
          <li>â€¢ Each message will open in a new tab with pre-filled text</li>
          <li>â€¢ Simply click send in each WhatsApp tab to deliver the message</li>
        </ul>
      </div>
    </div>
  )
}
