"use client"

import { useState } from 'react'
import Sidebar from '@/components/Sidebar'
import Header from '@/components/Header'
import DashboardWidgets from '@/components/DashboardWidgets'
import FollowUpTable from '@/components/FollowUpTable'
import InquiryFollowUpTable from '@/components/InquiryFollowUpTable'
import AppointmentPage from '@/components/AppointmentPage'
import PatientPage from '@/components/PatientPage'
import ReminderSystem from '@/components/ReminderSystem'

export default function Dashboard() {
  const [activeView, setActiveView] = useState('dashboard')

  return (
    <div className="flex h-screen bg-gray-50">
      <Sidebar activeView={activeView} setActiveView={setActiveView} />
      
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        
        <main className="flex-1 overflow-x-hidden overflow-y-auto bg-gray-50 p-6">
          {activeView === 'dashboard' && (
            <div className="space-y-6">
              <DashboardWidgets />
              
              <div className="grid grid-cols-1 gap-6">
                <FollowUpTable />
                <InquiryFollowUpTable />
              </div>
            </div>
          )}
          
          {activeView === 'new' && (
            <PatientPage />
          )}
          
          {activeView === 'old' && (
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">Old Patients</h2>
              <p className="text-gray-600">Old patient records coming soon...</p>
            </div>
          )}
          
          {activeView === 'account' && (
            <ReminderSystem />
          )}
          
          {activeView === 'inventory' && (
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">Inventory Management</h2>
              <p className="text-gray-600">Inventory management coming soon...</p>
            </div>
          )}
          
          {activeView === 'pharmacy' && (
            <div className="bg-white rounded-lg shadow p-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">Pharmacy Bill</h2>
              <p className="text-gray-600">Pharmacy billing coming soon...</p>
            </div>
          )}
          
          {activeView === 'physicians' && (
            <ReminderSystem />
          )}
        </main>
      </div>
    </div>
  )
}
